<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">공지 사항</h2>
      <div class="d-flex justify-content-between align-items-center">
        <div class="text-muted">Notice Management</div>
        <button
          class="btn btn-primary"
          v-if="isAdmin"
          @click="showModal('create')"
        >
          공지등록
        </button>
      </div>

      <!-- 테이블 보기 -->
      <table class="table mt-3" id="dataTable">
        <thead>
          <tr>
            <th>번호</th>
            <th>제목</th>
            <th>작성자</th>
            <th>작성일</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="item in paginatedData"
            :key="item.id"
            @click="showModal('view', item)"
          >
            <td>{{ item.id }}</td>
            <td>{{ item.title }}</td>
            <td>{{ item.author }}</td>
            <td>{{ item.date }}</td>
          </tr>
        </tbody>
      </table>

      <!-- 페이지네이션 -->
      <nav>
        <ul class="pagination justify-content-center page-botton">
          <li
            class="page-item"
            :class="{ disabled: currentPage === 1 }"
          >
            <a class="page-link" @click.prevent="changePage(currentPage - 1)">
              Previous
            </a>
          </li>
          <li
            v-for="page in totalPages"
            :key="page"
            class="page-item"
            :class="{ active: page === currentPage }"
          >
            <a class="page-link" @click.prevent="changePage(page)">
              {{ page }}
            </a>
          </li>
          <li
            class="page-item"
            :class="{ disabled: currentPage === totalPages }"
          >
            <a class="page-link" @click.prevent="changePage(currentPage + 1)">
              Next
            </a>
          </li>
        </ul>
      </nav>
    </div>

    <!-- 부트스트랩 모달 -->
    <div class="modal fade" id="noticeModal" tabindex="-1" aria-labelledby="noticeModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <!-- 모달 헤더 -->
          <div class="modal-header">
            <h5 class="modal-title" id="noticeModalLabel">공지사항</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <!-- 모달 본문 -->
          <div class="modal-body">
            <form>
              <div class="mb-3">
                <label for="noticeTitle" class="form-label">제목</label>
                <input type="text" class="form-control" id="noticeTitle" v-model="form.title" :disabled="!isAdmin" placeholder="제목을 입력하세요">
              </div>
              <div class="mb-3">
                <label for="noticeAuthor" class="form-label">작성자</label>
                <input type="text" class="form-control" id="noticeAuthor" v-model="form.author" :disabled="!isAdmin" placeholder="작성자를 입력하세요">
              </div>
              <div class="mb-3">
                <label for="noticeDate" class="form-label">작성일</label>
                <input type="date" class="form-control" id="noticeDate" v-model="form.date" disabled>
              </div>
              <div class="mb-3">
                <label for="noticeContent" class="form-label">내용</label>
                <textarea class="form-control" id="noticeContent" v-model="form.content" :disabled="!isAdmin" rows="4" placeholder="내용을 입력하세요"></textarea>
              </div>
              <div v-if="!isAdmin" class="mb-3 form-check">
                <input type="checkbox" class="form-check-input" id="cookieConsent" v-model="form.cookieConsent">
                <label class="form-check-label" for="cookieConsent">해당 공지를 다시 띄우지 않음(쿠키 사용 동의)</label>
              </div>
            </form>
          </div>
          <!-- 모달 푸터 -->
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">닫기</button>
            <button type="button" class="btn btn-primary" v-if="isAdmin && isCreate" @click="saveNotice">등록</button>
            <button type="button" class="btn btn-primary" v-if="isAdmin && !isCreate" @click="editNotice">수정</button>
            <button type="button" class="btn btn-danger" v-if="isAdmin && !isCreate" @click="confirmationModalVisible = true">삭제</button>
          </div>
        </div>
      </div>
    </div>


    <AlertModal 
      :isVisible="confirmationModalVisible" 
      :disableBackgroundClose="false"
      title="저장 확인" 
      confirmText="확인" 
      cancelText="취소"
      @close="confirmationModalVisible = false" 
      @confirm="deleteNoticeConfirm"
      class="custom-alert-modal">
      <template #body>
        <p>삭제하시겠습니까?</p>
      </template>
    </AlertModal>


  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import AlertModal from '@/components/modal/AlertModal.vue';
import * as bootstrap from 'bootstrap';

const modalInstance = ref(null);

const isAdmin = ref(true);
const isCreate = ref(true);

const currentPage = ref(1);
const itemsPerPage = 8;
const form = ref({
  id: null,
  title: '',
  author: '',
  date: '',
  content: '',
  cookieConsent: false,
});

const confirmationModalVisible = ref(false);
const selectedItemId = ref(null);

const data = ref([
  { id: 1, title: '공지사항 제목 1', author: '관리자A', date: '2024-08-01', content: '공지사항 내용 1' },
  { id: 2, title: '공지사항 제목 2', author: '관리자B', date: '2024-08-02', content: '공지사항 내용 2' },
  { id: 3, title: '공지사항 제목 3', author: '관리자C', date: '2024-08-03', content: '공지사항 내용 3' },
  { id: 4, title: '공지사항 제목 4', author: '관리자A', date: '2024-08-04', content: '공지사항 내용 4' },
  { id: 5, title: '공지사항 제목 5', author: '관리자D', date: '2024-08-05', content: '공지사항 내용 5' },
  { id: 6, title: '공지사항 제목 6', author: '관리자E', date: '2024-08-06', content: '공지사항 내용 6' },
  { id: 7, title: '공지사항 제목 7', author: '관리자F', date: '2024-08-07', content: '공지사항 내용 7' },
  { id: 8, title: '공지사항 제목 8', author: '관리자B', date: '2024-08-08', content: '공지사항 내용 8' },
  { id: 9, title: '공지사항 제목 9', author: '관리자C', date: '2024-08-09', content: '공지사항 내용 9' },
  { id: 10, title: '공지사항 제목 10', author: '관리자A', date: '2024-08-10', content: '공지사항 내용 10' },
  { id: 11, title: '공지사항 제목 11', author: '관리자D', date: '2024-08-11', content: '공지사항 내용 11' },
  { id: 12, title: '공지사항 제목 12', author: '관리자E', date: '2024-08-12', content: '공지사항 내용 12' },
]);

const paginatedData = computed(() => {
  const start = (currentPage.value - 1) * itemsPerPage;
  return data.value.slice(start, start + itemsPerPage);
});

const totalPages = computed(() => Math.ceil(data.value.length / itemsPerPage));

function getLocalDate() {
  const today = new Date();
  today.setMinutes(today.getMinutes() - today.getTimezoneOffset());
  return today.toISOString().split('T')[0];
}

function showModal(mode, item = null) {
  if (mode === 'create') {
    form.value = { id: null, title: '', author: '', date: getLocalDate(), content: '', cookieConsent: false };
    isCreate.value = true;
    selectedItemId.value = null;
  } else if (mode === 'view') {
    form.value = { ...item };
    isCreate.value = false;
    selectedItemId.value = item.id;
  }
  modalInstance.value.show();
}

function deleteNoticeConfirm() {
  if (selectedItemId.value !== null) {

    // selectedItemId에 해당하는 공지사항을 삭제
    // 추후 삭제 로직 추가...
    data.value = data.value.filter((item) => item.id !== selectedItemId.value);

    // 공지 모달 닫기
    resetModal();
  }
  confirmationModalVisible.value = false;
}

function resetModal() {
  modalInstance.value.hide();
}

function saveNotice() {
  // 추후 공지 저장 로직을 여기에 추가
  resetModal();
}

function editNotice() {
  // 추후 공지 수정 로직을 여기에 추가
  resetModal();
}

function changePage(page) {
  if (page > 0 && page <= totalPages.value) {
    currentPage.value = page;
  }
}

onMounted(() => {
  const modalElement = document.getElementById('noticeModal');
  modalInstance.value = new bootstrap.Modal(modalElement);
});

</script>

<style scoped>
.custom-alert-modal {
  z-index: 1055; /* 부트스트랩 모달 기본 z-index보다 높게 설정 */
}

.pagination {
  margin-top: 30px !important;
}

.modal-dialog {
  display: flex;
  align-items: center; /* 수직 중앙 정렬 */
  justify-content: center; /* 수평 중앙 정렬 */
  min-height: calc(100vh - 1rem); /* 화면 높이에 따라 중앙 정렬 */
}

/* 테이블 행에 마우스를 올렸을 때 배경색 변경 */
#dataTable tbody tr:hover td {
  background-color: #e7f1ff !important;
  /* font-weight: bold; 폰트를 볼드체로 변경
  text-decoration: underline; 텍스트에 밑줄 추가 */
}
/* #dataTable tbody tr:hover td a {
  color: #ffcc00 !important; 링크의 색상을 노란색으로 변경
  text-decoration: none; 링크의 밑줄을 제거
} */
</style>
