<template>
  <div class="content">
    <h2 class="content-title">부서 및 팀 관리</h2>
    <p class="content-sub-title">부서 및 팀을 관리하는 페이지입니다.</p>

    <p class="department-coment">새로운 부서 추가</p>

    <!-- 부서 추가 -->
    <div class="d-flex justify-content-between align-items-center mt-4 department-search-line" style="width: 100%;">
      <div class="input-group" style="width: 40%;">
        <input v-model="newDepartment" type="text" class="form-control department-input" placeholder="부서 이름" />
        <button class="btn btn-primary" @click="addDepartment">부서 추가</button>
      </div>
      <!-- 저장 버튼 오른쪽 정렬 -->
      <button class="btn btn-success ms-3" style="white-space: nowrap;" @click="saveDepartments">저장</button>
    </div>

    <hr />

    <!-- 부서 및 팀 관리 아코디언 -->
    <div id="departmentAccordion" class="department-list">
      <div class="accordion" v-for="(department, index) in departments" :key="index">
        <div class="accordion-item">
          <h2 class="accordion-header" :id="'heading' + index">
            <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
              :data-bs-target="'#collapse' + index" aria-expanded="false" :aria-controls="'collapse' + index">
              <strong>{{ department.name }}</strong>
            </button>
            <button class="btn btn-sm btn-outline-danger ms-2 bnt-department-delete" @click="openDeleteDepartmentModal(index)">
              부서 삭제
            </button>
          </h2>
          <div :id="'collapse' + index" class="accordion-collapse collapse" :aria-labelledby="'heading' + index"
            data-bs-parent="#departmentAccordion">
            <div class="accordion-body">
              <ul class="list-group">
                <li v-for="(team, teamIndex) in department.teams" :key="teamIndex"
                  class="list-group-item d-flex justify-content-between align-items-center">
                  {{ team }}
                  <span>
                    <button class="btn btn-sm btn-outline-primary me-2 list-item" @click="openEditTeamModal(index, teamIndex)">
                      수정
                    </button>
                    <button class="btn btn-sm btn-outline-danger list-item" @click="openDeleteTeamModal(index, teamIndex)">
                      삭제
                    </button>
                  </span>
                </li>
              </ul>

              <!-- 팀 추가 -->
              <div class="mt-3">
                <input v-model="newTeam" class="form-control" placeholder="새로운 팀 추가" />
                <button class="btn btn-success mt-2 btn-team-add" @click="addTeam(index)">
                  팀 추가
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <AlertModal 
      :isVisible="confirmationDepartmentModalVisible" 
      :disableBackgroundClose="true"
      title="부서 삭제 확인" 
      cancelText="취소"
      confirmText="확인" 
      @close="confirmationDepartmentModalVisible = false" 
      @confirm="deleteDepartment"
      class="custom-alert-modal">
      <template #body>
        <p>정말로 부서를 삭제하시겠습니까?</p>
      </template>
    </AlertModal>

    <AlertModal 
      :isVisible="confirmationTeamModalVisible" 
      :disableBackgroundClose="true"
      title="팀 삭제 확인" 
      cancelText="취소"
      confirmText="확인" 
      @close="confirmationTeamModalVisible = false" 
      @confirm="deleteTeam"
      class="custom-alert-modal">
      <template #body>
        <p>정말로 팀을 삭제하시겠습니까?</p>
      </template>
    </AlertModal>

    <EditModal 
      :isVisible="editTeamModalVisible"
      title="팀 수정"
      :value="editingTeam"
      confirmText="확인"
      cancelText="취소"
      @close="editTeamModalVisible = false"
      @confirm="confirmEditTeam"
      @update:value="editingTeam = $event"
    ></EditModal>

    <!-- 저장 확인 모달 -->
    <AlertModal 
      :isVisible="confirmSaveModalVisible"
      confirmText="확인"
      cancelText="취소"
      @close="confirmSaveModalVisible = false"
      @confirm="confirmSaveDepartments"
      class="custom-alert-modal">
      <template #body>
        <p style="color: red;"><strong>※ 주의 ※</strong></p>
        <p style="color: red;">부서, 직책 수정 및 삭제 시 기존 유저는 미지정으로 등록되며 등록을 다시 해줘야 합니다. 진행하시겠습니까?</p>
      </template>
    </AlertModal>

    <!-- 저장 완료 여부 -->
    <AlertModal 
      :isVisible="isModalVisible" 
      :disableBackgroundClose="true"
      :title="modalTitle" 
      confirmText="확인" 
      @confirm="isModalVisible = false"
      class="custom-alert-modal">
      <template #body>
        <p>{{ modalMessage }}</p>
      </template>
    </AlertModal>

  </div>
</template>

<script setup>
import { ref } from 'vue';
import axios from 'axios';
import AlertModal from '@/components/modal/AlertModal.vue';
import EditModal from '@/components/modal/EditModal.vue';

// 부서 및 팀 데이터
const departments = ref([
  { name: 'SI사업본부', teams: ['프레임워크','은행'] },
  { name: 'AI사업본부', teams: ['RPA','빅데이터'] },
  { name: '경영관리부', teams: ['영업','경영'] },
]);

// 새로운 부서 및 팀 추가를 위한 변수
const newDepartment = ref('');
const newTeam = ref('');

// 모달 표시를 위한 상태 변수
const isModalVisible = ref(false);
const modalTitle = ref('');
const modalMessage = ref('');

const confirmationDepartmentModalVisible = ref(false);
const confirmationTeamModalVisible = ref(false);
const confirmSaveModalVisible = ref(false); // 저장 확인 모달 상태 추가
const selectedDepartmentIndex = ref(null);
const selectedTeamIndex = ref(null);

// 수정 모달
const editTeamModalVisible = ref(false);
const editingTeam = ref('');

// 부서 삭제 오픈 모달 함수
const openDeleteDepartmentModal = (index) => {
  selectedDepartmentIndex.value = index;
  confirmationDepartmentModalVisible.value = true;
};

// 부서 추가 함수
const addDepartment = () => {
  if (newDepartment.value.trim() !== '') {
    departments.value.push({ name: newDepartment.value, teams: [] });
    newDepartment.value = ''; // 입력 필드 초기화
  }
};

// 부서 삭제 함수
const deleteDepartment = () => {
  if (selectedDepartmentIndex.value !== null) {
    departments.value.splice(selectedDepartmentIndex.value, 1);
    confirmationDepartmentModalVisible.value = false;
    selectedDepartmentIndex.value = null; // 초기화
  }
};

// 팀 추가 함수
const addTeam = (departmentIndex) => {
  if (newTeam.value.trim() !== '') {
    departments.value[departmentIndex].teams.push(newTeam.value);
    newTeam.value = ''; // 입력 필드 초기화
  }
};

// 팀 삭제 확인 모달 오픈 함수
const openDeleteTeamModal = (departmentIndex, teamIndex) => {
  selectedDepartmentIndex.value = departmentIndex;
  selectedTeamIndex.value = teamIndex;
  confirmationTeamModalVisible.value = true;
};

// 팀 삭제 함수
const deleteTeam = () => {
  if (selectedDepartmentIndex.value !== null && selectedTeamIndex.value !== null) {
    departments.value[selectedDepartmentIndex.value].teams.splice(selectedTeamIndex.value, 1);
    confirmationTeamModalVisible.value = false;
    selectedDepartmentIndex.value = null;
    selectedTeamIndex.value = null; // 초기화
  }
};

// 팀 수정 모달 오픈 함수
const openEditTeamModal = (departmentIndex, teamIndex) => {
  selectedDepartmentIndex.value = departmentIndex;
  selectedTeamIndex.value = teamIndex;
  editingTeam.value = departments.value[departmentIndex].teams[teamIndex];
  editTeamModalVisible.value = true;
};

// 팀 수정 확인 함수
const confirmEditTeam = () => {
  if (selectedDepartmentIndex.value !== null && selectedTeamIndex.value !== null && editingTeam.value.trim() !== '') {
    departments.value[selectedDepartmentIndex.value].teams[selectedTeamIndex.value] = editingTeam.value;
  }
  editTeamModalVisible.value = false;
};

// 저장 확인 모달 열기
const saveDepartments = () => {
  confirmSaveModalVisible.value = true; // 저장 확인 모달 열기
};

// 저장 확인 후 실제 저장 작업 수행
const confirmSaveDepartments = () => {
  axios
    .post('/api/departments', { departments: departments.value })
    .then((response) => {
      console.log('저장 성공:', response.data);
      modalTitle.value = '저장 완료';
      modalMessage.value = '부서 및 팀이 성공적으로 저장되었습니다.';
      isModalVisible.value = true; // 모달 표시
    })
    .catch((error) => {
      console.error('저장 실패:', error);
      modalTitle.value = '저장 실패';
      modalMessage.value = '저장에 실패했습니다. 다시 시도해 주세요.';
      isModalVisible.value = true; // 모달 표시
    });
  confirmSaveModalVisible.value = false; // 저장 확인 모달 닫기
};
</script>


<style scoped>
.accordion-item {
  margin-bottom: 1rem;
}

.department-coment {
  margin: 0 0 10px 3px !important;
  font-weight: 500;
}

.list-group-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.mt-5 {
  margin: 0px !important;
}

.bnt-department-delete {
  margin-bottom: 5px;
}

.accordion-header {
  margin-bottom: 3px;
}

hr {
  margin-top: 0px;
}

input {
  margin-right: 5px;
}

.department-search-line {
  margin: 0 0 10px 0 !important;
}

.accordion-body {
  padding-top: 5px;
}

/* 스크롤 가능하도록 설정 */
.department-list {
  max-height: 400px; /* 스크롤 가능한 높이 설정 */
  overflow-y: auto;
  padding-right: 10px;
}

@media (max-width: 768px) {
  .accordion-button {
    font-size: 0.9rem !important;
    margin-bottom: 10px;
  }
  
  .list-group-item {
    font-size: 0.7rem !important;
  }
  
  .list-item, .btn-team-add {
    font-size: 0.65rem !important;
    padding: 4px 8.5px !important;
  }

  .bnt-department-delete {
    font-size: 0.65rem !important;
    /* padding: 5px !important; */
  }

  .align-items-center {
    margin-bottom: 7px !important;
  }

  .mt-3 {
    margin-top: 0px !important;
  }
}
</style>
